import React from 'react';
import './main.scss';
import { Col, Container, Navbar, Row, Dropdown, Card, Button, Image } from 'react-bootstrap';
import mifoto from './mifoto.jpg';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowDown } from '@fortawesome/free-solid-svg-icons'

class Main extends React.Component {
    render() {
        return (
            <div className='welcome-section-container' id = 'me'>
                {/* Versión Mobile */}
                <Container className='img-container d-md-none'>
                    <Row>
                        <Col xs={6} md={4}>
                            <Image src={mifoto} rounded width='300px' height='300px' />
                        </Col>
                    </Row>
                </Container>
                <Container className='welcome-section d-md-none'>
                    <Row>
                        <Col xs={12}>
                            <h1> ¡Hola a Todos! <br />
                                Soy Ysnaldo Lopez
                                </h1>
                        </Col>
                        <Col xs={12} >
                            <p>Soy Ingeniero Industrial y un desarrollador front-end en formación, me gusta aprender cada dia mas sobre este mundo</p>
                        </Col>
                    </Row>
                </Container>
                <Container className='arrow-container d-md-none'>
                    <FontAwesomeIcon icon={faArrowDown} className='arrow-down' />
                </Container>
                {/* Version Tablet */}
                <Container className='welcome-section-container-secondary d-none d-md-block d-xl-none' >
                    <Row>
                        <Col xs={6} className='welcome-section-secondary'>
                            <h1> ¡Hola a Todos! <br />
                                Soy Ysnaldo Lopez
                                </h1>
                            <p>Soy Ingeniero Industrial y un desarrollador front-end en formación, me gusta aprender cada dia mas sobre este mundo</p>
                        </Col>
                        <Col xs={3} className='img-container-secondary'>
                            <Image src={mifoto} rounded width='400px' height='400px' />
                        </Col>
                    </Row>
                    <Container className='arrow-container-secondary'>
                        <FontAwesomeIcon icon={faArrowDown} className='arrow-down-secondary' />
                    </Container>
                </Container>
                {/* Version Desktop */}
                <Container className='welcome-section-container-secondary d-none d-xl-block' >
                    <Row>
                        <Col xs={6} className='welcome-section-secondary'>
                            <h1> ¡Hola a Todos! <br />
                                Soy Ysnaldo Lopez
                                </h1>
                            <p>Soy Ingeniero Industrial y un desarrollador front-end en formación, me gusta aprender cada dia mas sobre este mundo</p>
                            <Container className='arrow-container-secondary'>
                                <FontAwesomeIcon icon={faArrowDown} className='arrow-down-secondary' />
                            </Container>
                        </Col>
                        <Col xs={3} className='img-container-secondary'>
                            <Image src={mifoto} rounded width='540px' height='540px' />
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default Main;