import React from 'react';
import { Col, Container, Navbar, Row, Dropdown, } from 'react-bootstrap';
import styled from 'styled-components'
import './header.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import { faUserCheck } from '@fortawesome/free-solid-svg-icons'
import { faBriefcase } from '@fortawesome/free-solid-svg-icons'
import { faPhoneAlt } from '@fortawesome/free-solid-svg-icons'
import logo from './logo.png';


const Styledh1 = styled.h1`
    color: red;
`

class Header extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <React.Fragment>
                <Container fluid className='ps-0 pe-0 d-sm-none'>
                    <Row >
                        <Col xs={12} className='pe-0'>
                            <Navbar expand='lg' variant='dark' bg='dark' fixed='top' className='navbar-container'>
                                <Dropdown className='pe-4 desplegable-container'>
                                    <img src = {logo} width = "42px" height = "42px" className = 'logo-container'></img>
                                    <Dropdown.Toggle className='boton-desplegable'>
                                        <FontAwesomeIcon icon={faBars} />
                                    </Dropdown.Toggle>
                                    <Dropdown.Menu  >
                                        <Dropdown.Item href = '#me'>
                                            <FontAwesomeIcon icon = {faUserCheck} className='me-3'/>
                                            Sobre mi</Dropdown.Item>
                                        <Dropdown.Item href = '#proyectos'>
                                            <FontAwesomeIcon icon = {faBriefcase} className='me-3'/>
                                            Proyectos</Dropdown.Item>
                                        <Dropdown.Item href = '#contacto'>
                                            <FontAwesomeIcon icon = {faPhoneAlt} className='me-3'/>
                                            Contacto</Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                                <Navbar.Brand href='#' className='hv-container'> {this.props.name}</Navbar.Brand>
                            </Navbar>
                        </Col>
                    </Row>
                </Container>
                <Container fluid className='ps-0 pe-0 d-none d-sm-block'>
                    <Row >
                        <Col xs={12} className='pe-0'>
                            <Navbar expand="lg" variant="dark" bg="dark" fixed = 'top'>
                                <Container className = 'pe-0 ps-0'>
                                    <img src = {logo} width = "42px" height = "42px" className='me-3'></img>
                                    <Navbar.Brand href="#me">
                                        <FontAwesomeIcon icon = {faUserCheck} className='me-2'/>
                                        Sobre mi</Navbar.Brand>
                                    <Navbar.Brand href="#proyectos">
                                        <FontAwesomeIcon icon = {faBriefcase} className='me-2'/>
                                        Proyectos</Navbar.Brand >
                                    <Navbar.Brand href="#contacto">
                                        <FontAwesomeIcon icon = {faPhoneAlt} className='me-2'/>
                                        Contacto</Navbar.Brand>
                                    <Navbar.Brand href='#' className='hv-container' id = 'hv-cont'> {this.props.name}</Navbar.Brand>
                                </Container>
                            </Navbar>
                        </Col>
                    </Row>
                </Container>
            </React.Fragment>
        );
    }
}

export default Header;