import React from 'react'
import './footer.scss'
import { Col, Container, Navbar, Row, Dropdown, Card, Button, Image } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faFacebook, faGithub, faTwitter, faWhatsapp, faGoogle} from '@fortawesome/free-brands-svg-icons';


function Footer() {
    return (
        <React.Fragment >
            <Container fluid className = 'contact-container scont' id = 'contacto' >
                <Row>
                    <Col xs={12}>
                       <h2>¡Contactame!</h2>
                    </Col>
                    <Col xs = {12} className = 'icons-container'>
                            <a href="https://www.facebook.com/" className = 'pe-2 ps-2'>
                           <FontAwesomeIcon icon = {faFacebook}/>
                            </a>
                            <a href="https://github.com/YsnaldoXVen" className = 'pe-2 ps-2'>
                            <FontAwesomeIcon icon = {faGithub}/>
                            </a>
                            <a href="https://twitter.com/lopezh_yjose" className = 'pe-2 ps-2'>
                            <FontAwesomeIcon icon = {faTwitter}/>
                            </a>
                            <a href="ysnaldolopez@gmail.com" className = 'pe-2 ps-2'>
                            <FontAwesomeIcon icon = {faGoogle} />
                            </a>
                    </Col>
                </Row>
            </Container>
        </React.Fragment>
    )
}

export default Footer;